<template>
    <div class="container">
        <Title greeting-message="學院成員"></Title>
        <b-row>
            <b-col md="4 text-center" v-for="(item, index) in members" :key="index">
                <b-card
                    :title="item.name + ' ' + item.title"
                    img-top
                    tag="article"
                    class="mb-2"
                >
                    <b-card-text>
                        <b-avatar src="https://placekitten.com/300/300" size="10rem"></b-avatar>
                        <div class="my-3">
                            <table class="table text-start" v-if="item.position != '助教'">
                                <tbody>
                                    <tr>
                                        <td class="fix_width">職稱</td>
                                        <td>{{ item.position }}</td>
                                    </tr>
                                    <tr>
                                        <td>學歷</td>
                                        <td>{{ item.education }}</td>
                                    </tr>
                                    <tr>
                                        <td>研究領域</td>
                                        <td>{{ item.research }}</td>
                                    </tr>
                                </tbody>
                            </table>
                            <table class="table text-start" v-if="item.position == '助教'">
                                <tbody>
                                    <tr>
                                        <td class="fix_width">職稱</td>
                                        <td>{{ item.position }}</td>
                                    </tr>
                                    <tr>
                                        <td>承辦業務</td>
                                        <td>{{ item.research }}</td>
                                    </tr>
                                </tbody>
                            </table>
                            <table class="table text-start" v-if="item.position != '助教'">
                                <tbody>
                                    <tr>
                                        <td>Email</td>
                                        <td>{{ item.email }}</td>
                                    </tr>
                                    <tr>
                                        <td>TEL</td>
                                        <td>{{ item.tel }}</td>
                                    </tr>
                                    <tr>
                                        <td>Office</td>
                                        <td>{{ item.office }}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </b-card-text>
                </b-card>
            </b-col>
        </b-row>
    </div>
    <div class="text-center">
        <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
    </div>
</template>

<script>
import Title from '../components/Title.vue';
import moment from 'moment'
import { UserServer } from '../../providers/http-server';

export default {
    name: 'App',
    data(){
        return {
            members: []
        }
    },
    components: { 
        Title
    },
    created() {
        this.moment = moment;
        UserServer.ReadMember().then(
            (response) => this.members = response.data
        );        
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        }
    }
};
</script>

<style scoped>
@import '../../libs/page.css';
</style>